.input-search {
  border: 1px solid #000;
  border-radius: 8px;
  margin-bottom: 12px;
  padding: 10px 15px;
}

.btn-search {
  display: inline-block;
  border: 1px solid #000;
  background-color: #000;
  border-radius: 8px;
  color: #fff;
  padding: 8px 10px;
  cursor: pointer;
}

@media screen and (max-width: 680px) {
  .btn-search {
    width: 250px;
  }
}

@media screen and (min-width: 680px) {
  .input-search {
    border-radius: 0;
    width: 40%;
    padding: 12px 28px;
  }
  .btn-search {
    border-radius: 0px 8px 8px 0px;
  }
}