.navbar {
  background-color: #fff;
  /* color: #fff;
  font-size: 14px; */
  width: 100%;
  max-width: 100px;
  min-height: 100%;
  display: flex;
  flex-flow: column nowrap;
}

.ul-navbar {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: row wrap;
}

.li-navbar {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  flex: 70%;
  padding: 20% 0px;
  font-size: 12px;
  text-align: center; 
  text-decoration: none;
  color: #000;
}

.li-navbar.active {
  /* background: #fff;
  color: #000; */
  font-weight: bold;
}

.li-navbar:hover, .li-navbar:hover > a {
  font-weight: bold;
}

.menu-navbar {
  width: 100%;
  max-width: 100px;
  padding: 10px;
  border: 1px solid #000;
  background: none;
  color: #000;
}

.menu-navbar:active {
  background: #000;
  color: #fff;
}

@media only screen and (max-width: 680px) {
  .navbar {
    max-width: 100%;
    min-height: auto;
  }

  .li-navbar {
    flex: 20%;
    padding: 8px 0;
    /* font-size: 12px; */
  }
} 

@media only screen and (min-width: 680px) {
  .navbar {
    justify-content: center;
    background: #000;
  }

  .li-navbar {
    color: #fff;
  }
} 
 