.cast {
  display: flex;
  flex-flow: row nowrap;
  /* overflow: auto; */
  width: 100vw;
}

.image-cast {
  flex: 1;
  padding: 8px 12px;
}

.image-cast img {
  /* width: 100%; */
  max-width: 150px;
}

/* .image-cast img,
.no-profile {
  opacity: 75%;
} */

.no-profile {
  min-width: 150px;
  height: 225px;
  background: gray;
}

.character-cast {
  font-size: 12px;
  text-align: left;
}

.name-cast {
  text-align: left;
  color: #fff;
  font-size: 14px;
  /* opacity: 90%; */
}

@media only screen and (max-width: 680px) {
  .image-cast img {
    max-width: 100px;
  }

  .no-profile {
    min-width: 100px;
    height: 150px;
  }
}