.jumbotron {
  width: 100%;
  height: 70vh;
  overflow: auto;
  padding: 0 5%;
  border: 1px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.jumbo-body {
  text-align: center;
}

.jumbo-title {
  font-size: 1.8em;
  font-family: monospace;
  margin-bottom: 5px
}

@media only screen and (max-width: 600px) {
  .jumbotron {
    height: 360px;
  }

}

@media only screen and (min-width: 680px) {
  .jumbo-title {
    font-size: 3em;
  }

  .jumbo-body {
    width: 100%;
  }
}