footer {
  width: 100%;
  height: auto;
  padding: 15px 10px;
  background: #000;
  color: #fff;
}

footer p {
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  /* font-style: italic; */
  font-weight: bold;
  letter-spacing: 3px;
}

.content-footer {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.icon-footer {
  padding: 5px 10px;
}