.comp-pagination {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 7px;
  /* max-width: 100px; */
}

.comp-pagination a {
  padding: 5px 10px;
  margin: 10px 0;
  text-align: center;
  text-decoration: none;
  font-weight: lighter;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #000;
}

.comp-pagination a.arrow {
  font-weight: bold;
}

/* .comp-pagination a.active {
  border: 1px solid #000;
  border-radius: 50%;
  font-weight: bold;
} */

.comp-pagination a:hover {
  font-weight: bold;
}

@media screen and (min-width: 680px) {
  .comp-pagination {
    flex-flow: column nowrap;
    justify-content: center;
    max-width: 35px;
    min-height: 95%;
    margin: 0 auto;
  }

  .comp-pagination a {
    padding: 5px 3px;
  }
}