* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.detail-video {
  grid-area: detail-video;
}

.detail-information {
  grid-area: detail-information;
}

.container-detail,
.detail-information {
  min-width: 100vw;
}

.fixed-home {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 999;
  margin: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid #fff;
  text-align: center;
  line-height: 65px;
}

.fixed-home:hover {
  background: #000;
  border: none;
}

.container-detail {
  background: #202020;
  /* font-size: 12px; */
  line-height: 1.5;
  display: grid;
  grid-template-areas: 'detail-video''detail-information';
  grid-gap: 15px;
}

.nav-detail {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 5vh 0;
  padding: 5px 0;
  position: sticky;
  top: 0;
  background: #202020;
  z-index: 99;
}

.nav-detail a {
  padding: 5px 10px;
  color: #fff;
  text-decoration: none;
  font-family: monospace;
  font-size: 1.5em;
  margin: 0 8px;
}

.nav-detail .nav-active-detail {
  border-bottom: 3px solid #fff;
  font-weight: bold;
}

.nav-detail a:hover:not(.active) {
  border-bottom: 3px solid #fff;
}

.nav-detail .nav-home:hover {
  border-bottom: 0;
}

.container-details,
.container-review {
  padding: 0 16px;
}

/* REVIEW STYLES */
.container-review {
  margin: 7vh 0;
}

/* RATE STYLES */
.container-rate {
  display: flex;
  justify-content: center;
  margin: 10vh;
}

.content-rate {
  display: flex;
  flex-flow: column nowrap;
  max-width: 50vw;
}

.content-rate img {
  width: 100%;
  border-radius: 3%;
  /* opacity: 90%; */
}

.icon-rate {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin: 2vh 0;
}

.icon-rate button {
  margin: 0;
  padding: 0;
  border: 0;
  background: #202020;
}

.rate-submit {
  padding: 6px;
  border: 1px solid #121212;
  color: #000;
  text-align: center;
  border-radius: 20px;
  background: #fff;
}

/* .rate-submit {
  opacity: 67%;
}

.rate-submit:hover {
  opacity: 87%;
} */

.desc-rate {
  color: #9a9fa9;
  font-size: 14px;
  text-align: center;
}

@media only screen and (min-width: 680px) {
  .nav-detail a {
    margin: 0 3vw;
  }

  /* RATE STYLES */
  .rate-1,
  .rate-2,
  .rate-3,
  .rate-4,
  .rate-5 {
    margin: 3vh 0;
    font-size: 1.6em;
  }

  .rate-submit {
    padding: 15px 8px;
    border-radius: 30px;
    font-size: 1.3em;
  }

  .content-rate {
    max-width: 30vw;
  }

  .desc-rate {
    font-size: 1.5em;
  }

  /* REVIEW STYLES */
  .container-review {
    display: flex;
    flex-flow: row wrap;
  }
}