.container-info {
  display: flex;
  flex-flow: column nowrap;
  /* border: 1px solid #000; */
  padding: 0 5px;
  margin: 15px 0;
  min-width: 100%;
  overflow: auto;
}

.title-info {
  padding: 8px 5px;
  font-weight: bold;
  letter-spacing: 3px;
  color: #fff;
  /* opacity: 85%; */
}

.content-info {
  padding: 5px;
  font-weight: lighter;
  text-align: justify;
  line-height: 1.8;
  letter-spacing: 2px;
  color: #9a9fa9;
  overflow: auto;
  /* max-width: 87vw; */
}