.card {
  /* border: 1px solid #000; */
  width: 150px;
  height: auto;
  /* background: #000; */
  color: #000;
}

.image {
  position: relative;
  height: 250px;
}

.image img {
  width: 100%;
  height: 250px;
  border-radius: 3%;
}

.image .no-image {
  background: black;
  height: 250px;
  border-radius: 3%;
  /* text-align: center; */
}

.contents {
  display: flex;
  flex-flow: column nowrap;
  padding: 5px 0;
}

.card-title h1 {
  font-family: monospace;
  font-size: 1.2em;
  /* margin-right: 5px; */
}

.card-date {
  font-family: monospace;
  font-size: 1.2em;
}

.card-rate {
  font-size: .9em;
  font-weight: bold;
  font-style: italic;
  text-align: center;
  line-height: 40px;
  width: 40px;
  height: 40px;
  position: absolute;
  right: 0;
  bottom: 0;
  background: #fff;
  border-radius: 10% 0 0 0;
  /* border-radius: 50%; */
}

@media screen and (max-width: 680px) {
  .card {
    max-width: 150px;
    margin: 0 auto;
    /* max-height: 330px; */
    /* font-size: 12px; */
  }

  /* .card-title {
    padding-right: 5px;
    text-align: left;
  } */

  .card-title h1,
  .card-date {
    font-size: 12px;
  }
}

@media screen and (min-width: 680px) {
  .card-title {
    max-width: max-content;
  }
}